<template>
  <div class="logoWrap">
    <div  class="loginSet pre">
      <div class="logo">
        <img src="../../../static/logo.png" />
        <div class="name">Ethernet Dapp</div>
      </div>
      <div class="form flex flexdc flexjcfe">
        <div class="createAccount btn flexcenter" @click="toNav('create')">创建账号</div>
        <div class="renewAccount btn flexcenter" @click="toNav('recover')">恢复账号</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      
    };
  },
  components: {},
  methods: {
    toNav(type) {
      if(type=="recover"){
          this.$router.push('/recover')
      }else if(type=="create"){
          this.$router.push('/create')
      }
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
@import "../../styles/login.scss";
</style>    



